import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Snackbar from '@material-ui/core/Snackbar';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Switch from '@material-ui/core/Switch';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { bleDevice } from './ble'
import { PageIsVoiceRecogResult } from './pageVoiceRecogResult';
import { PageIsVoiceRecogSettings } from './pageVoiceRecogSettings';
import { PageIsIndoorEnvironment } from './pageIndoorEnvironment';
import { PageIsInstructions } from './pageInstructions';
import { titleName, VOICERECOGMW } from './config';

const drawerWidth         = 200;
const isVoiceRecogResult  = 'V/R Result';
const isVoiceRecogSettigs = 'V/R Settings';
const isIndoorEnvironment = 'Indoor Environment';
const isLedControl        = 'LED Control';
const isInstructions      = 'Instructions';
const dummyOfParams = [0];

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up('sm')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  title: {
    flexGrow: 1,
    color: 'white',
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#333333',
  },
}));



function App(props) {
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  const bleConnected = React.useRef(0);
  const numOfPhrases = React.useRef(0);
  const countOfPhrases = React.useRef(0);
  const voiceRecogMwId = React.useRef(0);
  const voiceRecogParams = React.useRef({
    finishedFlag : false,
    master : [dummyOfParams],
    backup : [dummyOfParams],
  });
  const voiceRecogResultIsUpdated = React.useRef(0);
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [bleButtonClicked, setBleButtonClicked] = React.useState(false);
  const [connectionText, setConnectionText] = React.useState('CONNECT');
  const [snackbarState, setSnackbarState] = React.useState({
    open : false,
    vertical : 'bottom',
    horizontal: 'center',
    message : '',
    autoHideDuration : 5000,
  });
  const [currentPage, setCurrentPage] = React.useState(isVoiceRecogResult);
  const [openCircle, setOpenCircle] = React.useState(false);
  const [switchChecked, setSwitchChecked] = React.useState([]);
  const [voiceRecogResult, setVoiceRecogResult] = React.useState({
    phraseId: 0,
    status: 0,
    score: 0,
  });
  const [sensorData, setSensorData] = React.useState({
    temperature: 0.0,
    humidity: 0.0,
    co2: 0.0,
    tvoc: 0.0,
    iaq: 0.0,
  });
  const handleSwitchcheckedToggle = (value) => () => {
    const currentIndex = switchChecked.indexOf(value);
    const newChecked = [...switchChecked];

    if (currentIndex === -1) {
      newChecked.push(value);
      handleLedOnOff(1);
    } else {
      newChecked.splice(currentIndex, 1);
      handleLedOnOff(0);
    }

    setSwitchChecked(newChecked);
  };

  const handleSnackbarOpen = (text) => {
    setSnackbarState({...snackbarState, open : true, message : text});
  }

  const handleSnackbarClose = () => {
    setSnackbarState({...snackbarState, open: false});
  }

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleBleToggle = () => {
    if (!bleConnected.current) {
      /* Reset data */
      voiceRecogParams.current = {...voiceRecogParams.current, finishedFlag : false, master : [dummyOfParams], backup : [dummyOfParams]};
      /* Connect */
      bleDevice.connectDevice(bleDevice.bleInfo.device, bleDevice.options, handleOnDisconnected)
      .then(server => {        
        bleConnected.current = true;
        setBleButtonClicked(true);
        setOpenCircle(true);
        handleSnackbarOpen('Get the service');
        /* Get service */
        return bleDevice.getService(server, bleDevice.bleInfo.serviceInfo.uuid);
      })
      .then(service => {
        handleSnackbarOpen('Get the characteristic');
        /* Get characteristics */
        return bleDevice.getCharacteristics(service);
      })
      .then(characteristics => {
        /* Set characteristics */
        return bleDevice.setCharacteristics(characteristics);
      })
      .then(_ => {
        handleSnackbarOpen('Get Voice Recognition Information');
        /* Read voice recognition library information */
        return readVoiceRecognitionLibInformation();
      })
      .then(_ => {
        /* Read voice recognition parameters */
        return readVoiceRecognitionParameters();
      })
      .then(_ => {
        handleSnackbarOpen('Start notification');
        /* Start notifications */
        return bleDevice.startNotifications(bleDevice.bleInfo.characteristicInfo.voiceRecogResult, 
                                            handleVoiceRecognitionResult);
      })
      .then(_ => {
        /* start notifications */
        return bleDevice.startNotifications(bleDevice.bleInfo.characteristicInfo.envSensor, 
                                            handleEnvironmentalSensor);
      })
      .then(_ => {
        setSwitchChecked([]);
        return handleLedOnOff(0);
      })
      .then(_ => {
        bleConnected.current = true;
        setOpenCircle(false);
        setBleButtonClicked(false);
        handleSnackbarOpen('Measurement is started');
        setConnectionText('DISCONNECT');
      })
      .catch(error => {
        bleConnected.current = false;
        setConnectionText('CONNECT');
        setOpenCircle(false);
        setBleButtonClicked(false); 
        handleSnackbarOpen('' + error);
        console.log(error);
      });
    }
    else {
      setBleButtonClicked(true);
      bleConnected.current = false;
      setConnectionText('CONNECT');
      bleDevice.disconnect(bleDevice.bleInfo.device);
      setBleButtonClicked(false);
      handleSnackbarOpen('The device is disconnected');
    }
  }

  const handleOnDisconnected = () => {
    if (bleConnected.current) {
      bleConnected.current = false;
      setConnectionText('CONNECT');
      setOpenCircle(false);
      setBleButtonClicked(false); 
      handleSnackbarOpen('Unexpected disconnection');
      console.log('Unexpected disconnection');
    }
    else {
      bleConnected.current = false;
    }
  }  

  const readVoiceRecognitionLibInformation = () => {
    return bleDevice.readValue(bleDevice.bleInfo.device, bleDevice.bleInfo.characteristicInfo.voiceRecogInfo.characteristic)
    .then(value => {     
      /* Decode */
      voiceRecogMwId.current = value.getUint8(0);
      numOfPhrases.current = value.getUint8(1);

      /* Log */
      console.log('middleware ID : '        +  voiceRecogMwId.current);
      console.log('number of phrases : ' +  numOfPhrases.current);

      /* Set data */
      countOfPhrases.current = 0;
    })
  }

  const readVoiceRecognitionParameters = () => {
    return bleDevice.readValue(bleDevice.bleInfo.device, bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic)
    .then(value => {  
      countOfPhrases.current += 1;

      /* Decode */
      const resultOfMaster = decodeParamsData(value);
      const resultOfBackup = decodeParamsData(value);

      /* Set data */
      voiceRecogParams.current.master = [...voiceRecogParams.current.master, resultOfMaster];
      voiceRecogParams.current.backup = [...voiceRecogParams.current.backup, resultOfBackup];

      /* Check current phrase ID */
      if (!numOfPhrases.current) {
        return readVoiceRecognitionLibInformation()
        .then(_ => {
          return readVoiceRecognitionParameters();
        })
      }
      else if (countOfPhrases.current !== numOfPhrases.current) {
        return readVoiceRecognitionParameters();
      }
      else {
        /* Set flag */
        voiceRecogParams.current.finishedFlag = true;

        /* Sort */
        voiceRecogParams.current.master.sort(sortParams);
        voiceRecogParams.current.backup.sort(sortParams);

        return Promise.resolve();
      }
    })
  }

  const decodeParamsData = (value) => {
    let params = [];

    switch (voiceRecogMwId.current) {
      case VOICERECOGMW.isAmiVoice :
        params = [
          value.getUint8(0),                                    // Phrase ID 
          value.getUint8(1),                                    // Confidence Threshold
          (value.getUint8(2) << 8) + value.getUint8(3),         // Voice Detection Threshold
          value.getUint8(4),                                    // Gain
          value.getUint8(5),                                    // Noise Suppressor
          value.getUint8(6),                                    // Beam Forming                         
        ];
      break;

      case VOICERECOGMW.isVoiceTrigger :
        params = [
          value.getUint8(0),                                    // Phrase ID 
          (value.getUint8(1) << 8) + value.getUint8(2),         // aThreshold
          value.getUint8(3),                                    // frameSkip
          value.getUint8(4),                                    // Gain
          value.getUint8(5),                                    // Noise Suppressor
          value.getUint8(6),                                    // Beam Forming                         
        ];
      break;

      case VOICERECOGMW.isDSpotter :        
        params = [
          value.getUint8(0),                                    // Phrase ID 
          value.getUint8(1),                                    // Confidence Threshold
          setSignedValue(value.getUint8(2), value.getUint8(3)), // Rejection Level
          setSignedValue(value.getUint8(4), value.getUint8(5)), // SG Level
          setSignedValue(value.getUint8(6), value.getUint8(7)), // Fil Revel
          value.getUint8(8),                                    // Response Time
          value.getUint8(9),                                    // Gain                         
        ];
      break;

      default :
      break;
    }

    return params;
  }

  const sortParams = (a, b) => {
    if ((!a) || (!b)) {
      return;
    }
    else {
      return (a[0] - b[0]);
    }
  }

  const handleVoiceRecognitionResult = (event) => {
    const value = event.target.value;
    const result = {};

    /* Decode */
    switch (voiceRecogMwId.current) {
      case VOICERECOGMW.isAmiVoice :
        result.phraseId = value.getUint8(0);
        result.status   = value.getUint8(1);
        result.score    = value.getUint8(2);
        /* Set data */
        voiceRecogResultIsUpdated.current ^= 1;
        setVoiceRecogResult({...voiceRecogResult, phraseId : result.phraseId, status : result.status, score : result.score});
      break;

      case VOICERECOGMW.isVoiceTrigger :
        result.phraseId = value.getUint8(0);
        result.status   = value.getUint8(1);
        /* Set data */
        voiceRecogResultIsUpdated.current ^= 1;
        setVoiceRecogResult({...voiceRecogResult, phraseId : result.phraseId, status : result.status});
      break;

      case VOICERECOGMW.isDSpotter :
        result.phraseId = value.getUint8(0);
        result.status   = value.getUint8(1);
        /* Set data */
        voiceRecogResultIsUpdated.current ^= 1;
        setVoiceRecogResult({...voiceRecogResult, phraseId : result.phraseId, status : result.status});
      break;

      default :
      break;
    }

    /* Log */
    console.log('phraseId : ' + result.phraseId);
    console.log('status : '   + result.status);
    console.log('score : '    + result.score);
  }

  const handleEnvironmentalSensor = (event) => {
    const value = event.target.value;
    const result = {};

    /* Decode */
    result.temperature  = setSignedValue(value.getUint8(0), (value.getUint8(1) + (value.getUint8(2) * 0.01)));
    result.humidity     = (value.getUint8(3) << 8)  + value.getUint8(4)  + (value.getUint8(5) * 0.01);
    result.co2          = (value.getUint8(6) << 8)  + value.getUint8(7)  + (value.getUint8(8) * 0.01);
    result.tvoc         = (value.getUint8(9) << 8)  + value.getUint8(10) + (value.getUint8(11) * 0.01);
    result.iaq          = (value.getUint8(12) << 8) + value.getUint8(13) + (value.getUint8(14) * 0.01);

    /* Round */ 
    result.temperature  = Math.round(result.temperature * 100) / 100;
    result.humidity     = Math.round(result.humidity * 100) / 100;
    result.co2          = Math.round(result.co2 * 100) / 100;
    result.tvoc         = Math.round(result.tvoc * 100) / 100;
    result.iaq          = Math.round(result.iaq * 100) / 100;

    /* Log */
    console.log('Temperature : ' + result.temperature);
    console.log('Humidity : '    + result.humidity);
    console.log('CO2 : '         + result.co2);
    console.log('TVOC : '        + result.tvoc);
    console.log('IAQ : '         + result.iaq);

    /* Set data */
    setSensorData({...sensorData, temperature : result.temperature, 
                                  humidity : result.humidity,
                                  co2 : result.co2,
                                  tvoc : result.tvoc,
                                  iaq : result.iaq
                  });
  } 

  const setSignedValue = (sign, value) => {
    let returnValue;
    if (sign === 1) {
      returnValue = (-1) * value;
    }
    else {
      returnValue = value;
    }
    return returnValue;
  }

  const handlePageChanged = (text) => {
    setCurrentPage(text);

    if (mobileOpen) {
      handleDrawerToggle();
    }
    else {
      // nothing
    }
  }

  const handleLedOnOff = (onoff) => {
    return bleDevice.writeValueWithoutResponse (bleDevice.bleInfo.device, 
                                                bleDevice.bleInfo.characteristicInfo.ledControl.characteristic, 
                                                [onoff]);
  }

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <Divider />
      <List>
        <ListItem button key={isVoiceRecogResult} onClick={() => handlePageChanged(isVoiceRecogResult)}>
         <ListItemText primary={isVoiceRecogResult} />
        </ListItem>
        <ListItem button key={isVoiceRecogSettigs} onClick={() => handlePageChanged(isVoiceRecogSettigs)}>
          <ListItemText primary={isVoiceRecogSettigs} />
        </ListItem>
        <ListItem button key={isIndoorEnvironment} onClick={() => handlePageChanged(isIndoorEnvironment)}>
         <ListItemText primary={isIndoorEnvironment} />
        </ListItem>
        <ListItem>
          <ListItemText key={isLedControl} primary={isLedControl} />
          <ListItemSecondaryAction>
            <Switch
              edge='end'
              onChange={handleSwitchcheckedToggle(isLedControl)}
              checked={switchChecked.indexOf(isLedControl) !== -1}
              inputProps={{ 'aria-labelledby': isLedControl }}
              color='primary' 
            />
          </ListItemSecondaryAction>
        </ListItem>
      </List>
      {/* <Divider />
      <List>
        <ListItem button key={isInstructions} onClick={() => handlePageChanged(isInstructions)}>
          <ListItemText primary={isInstructions} />
        </ListItem>
      </List> */}
    </div>
  );


  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position='fixed' className={classes.appBar}>
        <Toolbar>
          <IconButton
            color='inherit'
            aria-label='open drawer'
            edge='start'
            onClick={handleDrawerToggle}
            className={classes.menuButton}
            style={{color:'white'}}>
            <MenuIcon />
          </IconButton>
          <Typography className={classes.title} variant='h6' noWrap>
            {titleName}
          </Typography>
          <Button 
          color='inherit' 
          style={{color:'white'}} 
          disabled = {Boolean(bleButtonClicked)}
          onClick={handleBleToggle}>
            {connectionText}
          </Button>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label='mailbox folders'>
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation='css'>
          <Drawer
            container={container}
            variant='temporary'
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation='css'>
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant='permanent'
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <DisplayPage 
        currentPage={currentPage} 
        voiceRecogMwId={voiceRecogMwId.current}
        voiceRecogResult={voiceRecogResult}
        voiceRecogResultIsUpdated={voiceRecogResultIsUpdated.current}
        voiceRecogParams={voiceRecogParams.current}
        sensorData={sensorData}
        />
      </main>
      <Snackbar
        open={snackbarState.open}
        onClose={handleSnackbarClose}
        message={snackbarState.message}
        key={snackbarState.vertical + snackbarState.horizontal}
        autoHideDuration={snackbarState.autoHideDuration}
      />
      <Backdrop className={classes.backdrop} open={openCircle} >
        <CircularProgress color='inherit' />
      </Backdrop>
    </div>
  );
}

function DisplayPage(props) {
  switch (props.currentPage) {
    case isVoiceRecogResult :
      return (
        <PageIsVoiceRecogResult 
        mwId = {props.voiceRecogMwId}
        phraseId = {props.voiceRecogResult.phraseId}
        status = {props.voiceRecogResult.status}
        score = {props.voiceRecogResult.score}
        dataIsUpdated = {props.voiceRecogResultIsUpdated}
        />
      );

    case isVoiceRecogSettigs : 
      return (
        <div>
          <PageIsVoiceRecogSettings
          mwId = {props.voiceRecogMwId} 
          finishedFlag={props.voiceRecogParams.finishedFlag}
          masterParams={props.voiceRecogParams.master} 
          backupParams={props.voiceRecogParams.backup}
          />
        </div>
      );

    case isIndoorEnvironment :
      return(
        <div>
          <PageIsIndoorEnvironment 
          sensorData={props.sensorData} 
          />
        </div>
      );

    case isInstructions :
      return (
        <div>
          <PageIsInstructions />
        </div>
        );

    default :
    return (
      <div>
        <Typography>Error</Typography>
      </div>
      );
  }
}


App.propTypes = {

  window: PropTypes.func,
};

export default App;