import React from 'react';
import PropTypes from 'prop-types';
import { Select } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Box from '@material-ui/core/Box';
import Snackbar from '@material-ui/core/Snackbar';
import { makeStyles } from '@material-ui/core/styles';
import { bleDevice } from './ble'
import { VOICERECOGMW } from './config';

const useStyles = makeStyles((theme) => ({
  box: {
    marginTop: theme.spacing(1.0),
    marginBottom: theme.spacing(0.5),
  },
  formControl: {
    marginTop: theme.spacing(1),
    minWidth: 120,
  },
  title: {
    fontSize: '150%',
    fontWeight: 'bold',
  },
  button: {
    marginTop: theme.spacing(2),    
    fontSize : '150%',
  }
}));

function PageIsVoiceRecogSettings (props) {
  const classes = useStyles();
  const [buttonClicked, setButtonClicked] = React.useState(false);
  const [snackbarState, setSnackbarState] = React.useState({
    open : false,
    vertical : 'bottom',
    horizontal: 'center',
    message : '',
    autoHideDuration : 1000,
  });

  const handleSnackbarOpen = (text) => {
    setSnackbarState({...snackbarState, open : true, message : text});
  }

  const handleSnackbarClose = () => {
    setSnackbarState({...snackbarState, open: false});
  }

  const sendParams = (phraseId) => {
    switch (props.mwId) {
      case VOICERECOGMW.isAmiVoice :
        return bleDevice.writeValueWithResponse(bleDevice.bleInfo.device,
                                                bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic,
                                                [
                                                  phraseId,                           // phrase ID
                                                  props.masterParams[phraseId][1],    // Confidence Threshold
                                                  (props.masterParams[1][2] >> 8),    // Voice Detection Threshold high 8 bits
                                                  (props.masterParams[1][2] & 0xFF),  // Voice Detection Threshold low 8 bits
                                                  props.masterParams[1][3],           // Gain
                                                  props.masterParams[1][4],           // Noise Suppressor
                                                  props.masterParams[1][5],           // Beam Forming
                                                ]);

      case VOICERECOGMW.isVoiceTrigger : 
        return bleDevice.writeValueWithResponse(bleDevice.bleInfo.device,
                                                bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic,
                                                [
                                                  phraseId,                                 // phrase ID
                                                  (props.masterParams[phraseId][1] >> 8),   // aThreshold high 8 bits
                                                  (props.masterParams[phraseId][1] & 0xFF), // aThreshold low 8 bits
                                                  props.masterParams[1][2],                 // frameSkip
                                                  props.masterParams[1][3],                 // Gain
                                                  props.masterParams[1][4],                 // Noise Suppressor
                                                  props.masterParams[1][5],                 // Beam Forming
                                                ]);
      case VOICERECOGMW.isDSpotter : 
        return bleDevice.writeValueWithResponse(bleDevice.bleInfo.device,
                                                bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic,
                                                [
                                                  phraseId,                                   // phrase ID
                                                  props.masterParams[phraseId][1],            // Confidence Threshold
                                                  getSigned(props.masterParams[1][2]),        // Sign of Rejection Level 
                                                  getAbsoluteValue(props.masterParams[1][2]), // Value of Rejection Level 
                                                  getSigned(props.masterParams[1][3]),        // Sign of SG Level 
                                                  getAbsoluteValue(props.masterParams[1][3]), // Value of SG Level 
                                                  getSigned(props.masterParams[1][4]),        // Sign of Fil Level 
                                                  getAbsoluteValue(props.masterParams[1][4]), // Value of Fil Level 
                                                  props.masterParams[1][5],                   // Response Time
                                                  props.masterParams[1][6],                   // Gain
                                                ]);

      default :
      return Promise.resolve();
    }
  }

  const getSigned = (value) => {
    let sign;
    if (value < 0) {
      sign = 1;
    }
    else {
      sign = 0;
    }
    return sign;
  }

  const getAbsoluteValue = (value) => {
    let absoluteValue = Math.abs(value);
    absoluteValue = Math.round(absoluteValue);
    return absoluteValue;    
  }

  const sendVoiceRecogParams = (phraseId) => {
    let currentParams;
    let previousParams;
    let sendFlag = false;

    if (props.finishedFlag === true) {
      if (phraseId < props.masterParams.length) {
        currentParams   = props.masterParams[phraseId];
        previousParams  = props.backupParams[phraseId];
        for (let paramId = 1; paramId < currentParams.length; paramId++) {
          if (currentParams[paramId] === previousParams[paramId]) {
            sendFlag = false;
          }
          else {
            sendFlag = true;
            break;
          }
        }

        if (sendFlag === true) {
          return sendParams(phraseId)
          .then(_ => {
            return sendVoiceRecogParams(phraseId + 1);
          })
        }
        else {
          return sendVoiceRecogParams(phraseId + 1);
        }
      }
      else {
        return Promise.resolve();
      }
    }
    else {
      return Promise.resolve();
    }
  }

  const updateParams = () => {
    let currentParam;
    let previousParam;

    switch (props.mwId) {
      case VOICERECOGMW.isAmiVoice :
        for (let phraseId = 1; phraseId < props.masterParams.length; phraseId++) {
          for (let paramId = 1; paramId < props.masterParams[phraseId].length; paramId++) {
            if ((phraseId > 1) && (paramId > 1)) { 
              break;
            }
            else {
              currentParam  = props.masterParams[phraseId][paramId];
              previousParam = props.backupParams[phraseId][paramId];
              if (currentParam === previousParam) {
                // nothing
              }
              else {
                /* Update the paramater */
                props.backupParams[phraseId][paramId] = currentParam;
              }
            }
          }
        }
      break;

      case VOICERECOGMW.isVoiceTrigger :
        for (let phraseId = 1; phraseId < props.masterParams.length; phraseId++) {
          for (let paramId = 1; paramId < props.masterParams[phraseId].length; paramId++) {
            if ((phraseId > 1) && (paramId > 1)) { 
              break;
            }
            else {
              currentParam  = props.masterParams[phraseId][paramId];
              previousParam = props.backupParams[phraseId][paramId];
              if (currentParam === previousParam) {
                // nothing
              }
              else {
                /* Update the paramater */
                props.backupParams[phraseId][paramId] = currentParam;
              }
            }
          }
        }
      break;

      case VOICERECOGMW.isDSpotter :
        for (let phraseId = 1; phraseId < props.masterParams.length; phraseId++) {
          for (let paramId = 1; paramId < props.masterParams[phraseId].length; paramId++) {
            if ((phraseId > 1) && (paramId > 1)) { 
              break;
            }
            else {
              currentParam  = props.masterParams[phraseId][paramId];
              previousParam = props.backupParams[phraseId][paramId];
              if (currentParam === previousParam) {
                // nothing
              }
              else {
                /* Update the paramater */
                props.backupParams[phraseId][paramId] = currentParam;
              }
            }
          }
        }        
      break;

      default :
      break;
    }
  }

  const handleSendButtonClicked = () => {
    if (props.finishedFlag === true) {
      setButtonClicked(true);

      /* Send data */
      return Promise.resolve()
      .then(_ => {
        return sendVoiceRecogParams(1);
      })
      .then(_ => {
        if (bleDevice.bleInfo.device.gatt.connected) {
          handleSnackbarOpen('Success');
          console.log('success');
        }
        else {
          console.log('No connection');
        }

        /* Update data */
        updateParams();

        setButtonClicked(false);
      })
      .catch(error => {
        handleSnackbarOpen('Failed');
        setButtonClicked(false);
        console.log(error);
      })
    }
    else {
      console.log('No params');
    }
  }

  return (
    <div>
      <DrawAmiVoice 
      mwId = {props.mwId}
      finishedFlag = {props.finishedFlag}
      masterParams = {props.masterParams} 
      backupParams = {props.backupParams}
      />

      <DrawVoiceTrigger
      mwId = {props.mwId}
      finishedFlag = {props.finishedFlag}
      masterParams = {props.masterParams} 
      backupParams = {props.backupParams}
      />

      <DrawDSpotter
      mwId = {props.mwId}
      finishedFlag = {props.finishedFlag}
      masterParams = {props.masterParams} 
      backupParams = {props.backupParams}
      />

      <Button 
      className = {classes.button} 
      variant = 'outlined' 
      color = 'primary' 
      fontWeight = 'bold'
      disabled = {Boolean(buttonClicked)}
      onClick = {handleSendButtonClicked}
      >
        APPLY
      </Button>

      <Snackbar
        open={snackbarState.open}
        onClose={handleSnackbarClose}
        message={snackbarState.message}
        key={snackbarState.vertical + snackbarState.horizontal}
        autoHideDuration={snackbarState.autoHideDuration}
      />

    </div>
  );
}

/** AmiVoice */
function DrawAmiVoice (props) {
  return (
    <div>
      {
        ((props.mwId === VOICERECOGMW.isAmiVoice)
        ?
          (
            <div>
              <DrawParamWithPhraseId
              finishedFlag  = {props.finishedFlag}
              masterParams  = {props.masterParams} 
              backupParams  = {props.backupParams}
              paramId       = {1} 
              paramName     = {'Confidence Threshold'}
              minValue      = {0}
              maxValue      = {200}              
              />

              <DrawParamWithoutPhraseId
              finishedFlag  = {props.finishedFlag}
              masterParams  = {props.masterParams} 
              backupParams  = {props.backupParams} 
              paramId       = {2}
              paramName     = {'Voice Detection Threshold'}
              minValue      = {0}
              maxValue      = {15000}              
              />

              <DrawParamWithoutPhraseId
              finishedFlag  = {props.finishedFlag}
              masterParams  = {props.masterParams}
              backupParams  = {props.backupParams}
              paramId       = {3}
              paramName     = {'Gain'}
              minValue      = {0}
              maxValue      = {9}              
              />

              <DrawParamWithoutPhraseId
              finishedFlag  = {props.finishedFlag}
              masterParams  = {props.masterParams} 
              backupParams  = {props.backupParams} 
              paramId       = {4}
              paramName     = {'Noise Suppressor'}
              minValue      = {0}
              maxValue      = {7}              
              />

              <DrawParamWithoutPhraseId
              finishedFlag  = {props.finishedFlag}
              masterParams  = {props.masterParams} 
              backupParams  = {props.backupParams} 
              paramId       = {5}
              paramName     = {'Beam Forming'}
              minValue      = {0}
              maxValue      = {7}              
              />

            </div>
          ) 
        :
          (
            <div></div>
          )
        )
      }

    </div>
  );
}

/** VoiceTrigger */
