import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import ledBlack from './img/led_black.png';
import ledRed from './img/led_red.png';
import ledGreen from './img/led_green.png';
import ledBlue from './img/led_blue.png';
import fanStop from './img/fan_stop.png';
import fanNormalSpeed from './img/fan_normal_speed.gif';
import fanLowSpeed from './img/fan_low_speed.gif';
import fanHighSpeed from './img/fan_high_speed.gif';
import fanSwing from './img/fan_swing.gif';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import { VOICERECOGMW, RENESASCOLOR, RECOGRESULT } from './config';

const resultInfo = {
  dataIsUpdated : 0,
}

const imageInfo = {
  led : {
    image : [
      ledBlack,
      ledRed,
      ledGreen,
      ledBlue,  
    ],
    length : 3,
  },
  electricFan : {
    image : [
      fanStop,
      fanNormalSpeed,
      fanSwing,
      fanHighSpeed,
      fanLowSpeed,
    ],
    length : 4,
  },
  dataIsUpdated : 0,
};

const logInfo = {
  text : [],
  maxLength : 100,
  dataIsUpdated : 0,
}

const useStyles = makeStyles((theme) => ({
  ledImg : {
    width : '25%',
    height: '25%',
    marginTop : theme.spacing(5),
    marginBottom : theme.spacing(5),
    marginRight : theme.spacing(10)
  },
  electricFanImg : {
    width : '25%',
    height: '25%',
    marginTop : theme.spacing(5),
    marginBottom : theme.spacing(5),
  },
  libName : {
    fontSize: '150%', 
    fontWeight: 'bold',
    color: RENESASCOLOR.gray,
  },
  logList : {
    width: '100%',
    maxWidth: '100%',
    position: 'relative',
    overflow: 'auto',
    maxHeight: 200,
    backgroundColor: theme.palette.background.default,
  },
  logTitle : {
    fontSize: '150%', 
    fontWeight: 'bold', 
    color: RENESASCOLOR.gray,
    padding: 0,
    backgroundColor: 'inherit',
  },
  logText : {
    color: RENESASCOLOR.gray,
    padding: 0,
    backgroundColor: 'inherit',
  },
}));

const getCurrentDateText = () => {
  const dateFullSize = new Date();
  const year    = dateFullSize.getFullYear();
  const month   = dateFullSize.getMonth() + 1;
  const date    = dateFullSize.getDate();
  const hours   = dateFullSize.getHours();
  const minutes = dateFullSize.getMinutes();
  const seconds = dateFullSize.getSeconds();
  const text = '[' + year + '/' + month + '/' + date + ' ' + hours + ':' + minutes + ':' + seconds + ']';

  return text;
} 

function PageIsVoiceRecogResult (props) { 
  const classes = useStyles();
  const [middlewareNameText, setMiddlewareNameText] = React.useState('No Middleware');

  React.useEffect(() => {
    switch (props.mwId) {
      case VOICERECOGMW.isAmiVoice :
        setMiddlewareNameText('Middleware : AmiVoice');
      break;

      case VOICERECOGMW.isVoiceTrigger :
        setMiddlewareNameText('Middleware : VoiceTrigger');
      break;    
      
      case VOICERECOGMW.isDSpotter :
        setMiddlewareNameText('Middleware : DSpotter');
      break;

      default :
      break;
    }
  }, [props.mwId]);

  return (
    <div>
      <Box mt={1} mb={2}>
        <Typography className = {classes.libName}>
          {middlewareNameText}
        </Typography>

        <DrawVoiceRecogStatus 
        mwId = {props.mwId}
        phraseId = {props.phraseId}
        status = {props.status}
        score = {props.score}
        dataIsUpdated = {props.dataIsUpdated}
        />
      </Box>

      <Divider/>

      <DrawImage
      phraseId = {props.phraseId}
      status = {props.status}
      dataIsUpdated = {props.dataIsUpdated}
      />

      <Divider/>

      <DrawLogText 
      mwId = {props.mwId}
      phraseId = {props.phraseId}
      status = {props.status}
      score = {props.score}
      dataIsUpdated = {props.dataIsUpdated}
      />

    </div>
  );
}

function DrawVoiceRecogStatus(props) {
  const [resultsText, setResultsText] = React.useState('No results');
  const resultTextStyle = React.useRef({
    fontSize: '200%', 
    fontWeight: 'bold',
    color: RENESASCOLOR.gray,
  });

  const drawResultText = (text) => {
    switch (props.mwId) {
      case VOICERECOGMW.isAmiVoice :
        /* phrase ID */
        text += (' ID is ' + props.phraseId+ '.');
        /* Score */
        text += (' Score is ' + props.score + '.');
      break;

      case VOICERECOGMW.isVoiceTrigger :
        /* phrase ID */
        text += (' ID is ' + props.phraseId+ '.');
      break;

      case VOICERECOGMW.isDSpotter :
        /* phrase ID */
        text += (' ID is ' + props.phraseId+ '.');
        /* Score */
        text += (' Score is ' + props.score + '.');
      break;

      default :
      break;
    }

    return text;
  }

  React.useEffect(() => {
    let resText;
    const dataIsUpdated = props.dataIsUpdated;
    if (resultInfo.dataIsUpdated !== dataIsUpdated) {
      resultInfo.dataIsUpdated = dataIsUpdated;
      console.log('Data is updated.');

      switch (props.status) {
        case RECOGRESULT.isSuccess :
          resText ='OK.';          
          resText = drawResultText(resText);
          resultTextStyle.current = {...resultTextStyle.current, color : RENESASCOLOR.blue};
        break;
  
        case RECOGRESULT.isFailed :
          resText ='NG.';
          resText = drawResultText(resText);
          resultTextStyle.current = {...resultTextStyle.current, color : 'red'};
        break;
  
        default :
          resText ='No results'; 
        break;
      }
  
      /* Set text */
      setResultsText(resText); 

    }
    else {
      console.log('Data is not updated. ');
    }  
  }, [props.phraseId, props.status, props.score, props.dataIsUpdated]);

  return (
    <Typography style = {resultTextStyle.current}>
      {resultsText} 
    </Typography>
  );
}

function DrawImage (props) {
  const classes = useStyles();
  const [led, setLed] = React.useState(imageInfo.led.image[0]);
  const [electricFan, setElectricFan] = React.useState(imageInfo.electricFan.image[0]);
  const toggleElectricFan = React.useRef(false);
  const toggleElectricFanSwing = React.useRef(false);

  React.useEffect(() => {
    const dataIsUpdated = props.dataIsUpdated;
    if (imageInfo.dataIsUpdated !== dataIsUpdated) {
      imageInfo.dataIsUpdated = dataIsUpdated;
      console.log('Data is updated.');
      switch (props.status) {
        case RECOGRESULT.isSuccess :
          let phraseId = props.phraseId;
          if (phraseId <= imageInfo.led.length) {
            setLed(imageInfo.led.image[phraseId]);
          }
          else if (phraseId <= (imageInfo.led.length + imageInfo.electricFan.length)) {
            phraseId -= imageInfo.led.length;
            if (toggleElectricFan.current === false) {
              if (phraseId === 1) {
                setElectricFan(imageInfo.electricFan.image[phraseId]);
                toggleElectricFan.current = true;
              }
            } else {
              if (phraseId === 1) {
                setElectricFan(imageInfo.electricFan.image[0]);
                toggleElectricFan.current = false;
                toggleElectricFanSwing.current = false;
              } else {
                if (phraseId === 2) {
                  if(toggleElectricFanSwing.current === false) {
                    setElectricFan(imageInfo.electricFan.image[phraseId]);
                    toggleElectricFanSwing.current = true;
                  } else {
                    setElectricFan(imageInfo.electricFan.image[1]);
                    toggleElectricFanSwing.current = false;
                  }
                } else {
                  toggleElectricFanSwing.current = false;
                  setElectricFan(imageInfo.electricFan.image[phraseId]);
                }                
              }
            }
            // if(phraseId === 4) {
            //   if (toggleElectricFan.current == false) {
            //     setElectricFan(imageInfo.electricFan.image[phraseId]);
            //     toggleElectricFan.current = true;
            //   } else {
            //     setElectricFan(imageInfo.electricFan.image[0]);
            //     toggleElectricFan.current = false;
            //   }
            // } else {
            //   setElectricFan(imageInfo.electricFan.image[phraseId - imageInfo.led.length]);
            // }            
          }
          else {
            // nothing
          }
        break;
  
        default :
        break;
      }
    }
    else {
      console.log('Data is not updated.');
    }
  }, [props.phraseId, props.status, props.dataIsUpdated]);

  return (
    <div>
      <img className = {classes.ledImg} src={led} alt="ledImage" />
      <img className = {classes.electricFanImg} src={electricFan} alt="electricFanImage" />
    </div>
  );
}

function DrawLogText(props) {
  const classes = useStyles();
  const [logLength, setLogLength] = React.useState(logInfo.text.length);

  const drawLogText = (text) => {
    switch (props.mwId) {
      case VOICERECOGMW.isAmiVoice :
        /* phrase ID */
        text += props.phraseId;
        text += ' ';
        /* Score */
        text += props.score;
      break;

      case VOICERECOGMW.isVoiceTrigger :
        /* phrase ID */
        text += props.phraseId;
      break;

      case VOICERECOGMW.isDSpotter :
        /* phrase ID */
        text += props.phraseId;
        text += ' ';
        /* Score */
        text += props.score;
      break;

      default :
      break;
    }

    return text;
  }

  const setCurrentLog = (currentLog) => {
    let oldLog;
    if (logLength >= logInfo.maxLength) {
      oldLog = logInfo.text.slice(0, -1);
      logInfo.text = [currentLog, ...oldLog];
      setLogLength(logInfo.maxLength);
    }
    else {
      logInfo.text = [currentLog, ...logInfo.text];
      setLogLength(logLength + 1);
    } 
  }

  React.useEffect(() => {
    let currentLog;
    const dataIsUpdated = props.dataIsUpdated;
    if (logInfo.dataIsUpdated !== dataIsUpdated) {
      logInfo.dataIsUpdated = dataIsUpdated;
      console.log('Data is updated.');
      switch (props.status) {
        case RECOGRESULT.isSuccess :
          currentLog = getCurrentDateText() + ' OK ';      
          currentLog = drawLogText(currentLog);
          setCurrentLog(currentLog);  
        break;
  
        case RECOGRESULT.isFailed :
          currentLog = getCurrentDateText() + ' NG '; 
          currentLog = drawLogText(currentLog);
          setCurrentLog(currentLog);  
        break;
  
        default :
        break;
      }
    }
    else {
      console.log('Data is not updated.');
    }
  }, [props.phraseId, props.status, props.score, props.dataIsUpdated]);

  return (
    <Box >
      <List className = {classes.logList} subheader={<li />}>
        <ListSubheader className = {classes.logTitle}>
          {'Result Log'}
        </ListSubheader>
        {
          logInfo.text.map((text, index) => (
            <ListItem key = {index} className = {classes.logText}>
              <ListItemText primary = {text} />
            </ListItem>
          ))
        }
      </List>
    </Box>
  );
}

PageIsVoiceRecogResult.propTypes = {
    window: PropTypes.func,
  };

export { PageIsVoiceRecogResult };